<template>
  <div class="autherinfo">
    <div
      v-if="isLoading"
      class="loading"
    >

    </div>

    <div v-else>
      <div class="authersummay">
        <div class="topbar">
          <span>作者</span>
        </div>
        <div class="maincontent">
          <router-link :to="{name:'user_info',params:{name:userinfo.loginname}}">
            <img
              :src="userinfo.avatar_url"
              alt="头像"
            >
          </router-link>
          <span class="user_name">{{userinfo.loginname}}</span>
          <div class="integral">
            <span>积分：{{userinfo.score}}</span>
          </div>
          <div class="synopsis"></div>
        </div>

      </div>

      <div class="recent_topics">
        <div class="topbar">作者其它话题</div>
        <ul>
          <li v-for="list in topcilimitby5">
            <router-link :to="{name:'post_content',params:{id:list.id,name:list.author.loginname}}">
              {{list.title}}
            </router-link>
          </li>
        </ul>
      </div>
      <div class="recent_replies">
        <div class="topbar">作者最近回复</div>
        <ul>
          <li v-for="list in replylimitby5">
            <router-link :to="{name:'post_content',params:{id:list.id,name:list.author.loginname}}">
              {{list.title}}
            </router-link>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SlideBar",
  data() {
    return {
      isLoading: false,
      userinfo: {},
    };
  },
  methods: {
    getData() {
      this.$http
        .get(`user/${this.$route.params.name}`)
        .then((res) => {
          this.isLoading = false; //加载成功，去除动画
          this.userinfo = res.data.data;
        })
        .catch(function (err) {
          //处理返回失败后的问题
          console.log(err);
        });
    },
  },
  computed: {
    topcilimitby5() {
      if (this.userinfo.recent_topics) {
        return this.userinfo.recent_topics.slice(0, 5);
      }
    },
    replylimitby5() {
      if (this.userinfo.recent_replies) {
        return this.userinfo.recent_replies.slice(0, 5);
      }
    },
  },
  beforeMount() {
    this.isLoading = true;
    this.getData();
  },
};
</script>

<style scoped>
.authersummay,
.recent_replies,
.recent_topics {
  background-color: #fff;
}
.autherinfo {
  width: 328px;
  float: right;
  margin-top: 0;
}
li {
  padding: 3px 0;
}
.recent_replies ul,
.recent_topics ul {
  margin-top: 0px;
  margin-bottom: 0px;
  list-style: none;
  padding: 10px;
}

ul a {
  font-size: 14px;
  text-decoration: none;
  color: #778087;
}

.topbar {
  padding: 10px;
  background-color: #f6f6f6;
  height: 40px;
  font-size: 13px;
  margin-top: 10px;
}

img {
  height: 48px;
  width: 48px;
  border-radius: 3px;
  margin: 10px;
  vertical-align: middle;
}

.loginname {
  width: 100px;
  float: right;
  margin-top: 22px;
  margin-right: 159px;
  font-size: 14px;
}

.loginname a {
  text-decoration: none;
  color: #778087;
}

.authersummay .topbar {
  margin-top: 0px;
}
.maincontent .user_name {
  font-size: 16px;
  color: #778087;
}
.maincontent .integral {
  /* margin-top: 10px; */
  padding: 10px;
  font-size: 14px;
}
.maincontent .synopsis {
  padding: 0 10px 10px 10px;
  font-size: 13px;
}
</style>
